import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useDispatch, useSelector } from 'react-redux';
import { selectBankDetails } from '../../../../redux/Brand/GettingPaid/gettingPaidSelector';
import { setBankDetails } from '../../../../redux/Brand/GettingPaid/gettingPaidSlice';
import Select from 'react-select';
import { BankDetailsValidationSchema } from './ValidationSchema';

const accountTypeOption = [
    {
        value: 'saving',
        label: 'Savings',
    },
    { value: 'current', label: 'Current' },
];

const accountRoleOption = [
    {
        value: 'personal',
        label: 'Personal',
    },
    { value: 'business', label: 'Business' },
];

const categoryStyle = {
    control: (styles) => {
        return {
            ...styles,
            borderColor: '#ebbca2',
            boxShadow: 'none',
            minHeight: '40px',
            '&:hover': {
                boxShadow: 'none',
            },
        };
    },
    container: (style) => {
        return {
            ...style,
            marginTop: '5xp',
            marginRight: '1px',
        };
    },
};

const defaultValues = {
    accountType: accountTypeOption[0],
    accountRole: accountRoleOption[0],
};

export default function EditBankDetail({ setEditBankDetails }) {
    const {
        register,
        handleSubmit,
        control,
        setValue,
        formState: { errors },
    } = useForm({
        mode: 'onChange',
        resolver: yupResolver(BankDetailsValidationSchema),
        defaultValues,
    });

    const bankDetails = useSelector(selectBankDetails);
    const dispatch = useDispatch();

    useEffect(() => {
        const fields = [
            'accountHolderName',
            'accountType',
            'accountRole',
            'accountNumber',
            'routingNumber',
        ];

        fields.forEach((field) => setValue(field, bankDetails[field]));
    }, []);

    const onSubmit = (data) => {
        dispatch(setBankDetails(data));
        // setEditBankDetails(false);
    };

    return (
        <>
            <form onSubmit={handleSubmit(onSubmit)}>
                <h2 className="mt-5">Bank Details</h2>
                <div className="form-area">
                    <div className="form-input mb-4">
                        <label htmlFor="" className="form-label">
                            Name of the bank account holder&nbsp;
                            <span className="asterisk-red">*</span>
                        </label>
                        <input
                            type="text"
                            className="form-control mb-0"
                            name="accountHolderName"
                            placeholder="Jane Doe"
                            {...register('accountHolderName', {
                                required: true,
                            })}
                        />
                        {errors.accountHolderName && (
                            <span className="error-text">
                                {errors.accountHolderName?.message}
                            </span>
                        )}
                    </div>
                    <div className="form-input mb-4">
                        <label htmlFor="" className="form-label">
                            Bank account type&nbsp;
                            <span className="asterisk-red">*</span>
                        </label>
                        <Controller
                            name="accountType"
                            control={control}
                            render={({ field }) => (
                                <Select
                                    {...field}
                                    className="basic-single"
                                    classNamePrefix="select"
                                    styles={categoryStyle}
                                    components={{
                                        IndicatorSeparator: () => null,
                                    }}
                                    theme={(theme) => ({
                                        ...theme,
                                        colors: {
                                            ...theme.colors,
                                            primary25: '#fbf5f0',
                                            primary: '#bd6f34',
                                        },
                                    })}
                                    options={accountTypeOption}
                                />
                            )}
                        />
                        {errors.accountType && (
                            <span className="error-text">
                                {errors.accountType?.message}
                            </span>
                        )}
                    </div>

                    <div className="form-input mb-4">
                        <label htmlFor="" className="form-label">
                            Purpose&nbsp;<span className="asterisk-red">*</span>
                        </label>
                        <input
                            type="text"
                            className="form-control mb-0"
                            name="accountRole"
                            placeholder=""
                            disabled
                            {...register('accountRole', { required: true })}
                        />
                        {errors.accountRole && (
                            <span className="error-text">
                                {errors.accountRole?.message}
                            </span>
                        )}
                    </div>

                    <div className="form-input mb-4">
                        <label htmlFor="" className="form-label">
                            Account number&nbsp;
                            <span className="asterisk-red">*</span>
                        </label>
                        <input
                            type="number"
                            className="form-control mb-0"
                            name="accountNumber"
                            placeholder="12345678"
                            {...register('accountNumber', { required: true })}
                        />
                        {errors.accountNumber && (
                            <span className="error-text">
                                {errors.accountNumber?.message}
                            </span>
                        )}
                    </div>

                    <div className="form-input mb-4">
                        <label htmlFor="" className="form-label">
                            Routing number&nbsp;
                            <span className="asterisk-red">*</span>
                        </label>
                        <input
                            type="number"
                            className="form-control mb-0"
                            name="routingNumber"
                            placeholder="123456789"
                            {...register('routingNumber', { required: true })}
                        />
                        {errors.routingNumber && (
                            <span className="error-text">
                                {errors.routingNumber?.message}
                            </span>
                        )}
                        <small>
                            9-digit Routing Number of the account used for ACH
                            transactions.
                        </small>
                    </div>
                </div>
                <div className="form-area">
                    <div className="form-input form-submit">
                        <button className="button w-100">Save</button>
                    </div>
                </div>
            </form>
        </>
    );
}

EditBankDetail.propTypes = {
    setEditBankDetails: PropTypes.func,
};
